<template>
  <v-app>
    <v-navigation-drawer
      clipped
      permanent
      app>
      <v-list>
        <div
          v-for="(item, i) in drawerItems"
          :key="i">
          <v-list-tile
            v-if="item.subItems === undefined"
            :value="$router.history.current.path === item.route"
            @click="moveToRoute(i)">
            <v-list-tile-action>
              <v-icon v-html="item.icon"></v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title v-text="item.title"></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-group
            v-else
            :prepend-icon="item.icon"
            :value="$router.history.current.path.includes(item.route)"
            @click="moveToRoute(i)">
            <v-list-tile slot="activator">
              <v-list-tile-content>
                <v-list-tile-title v-text="item.title"></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>

            <v-list-tile
              :value="$router.history.current.path === item.route">
              <v-list-tile-content>
                <v-list-tile-title>메인</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile
              v-for="(subItem, i) in item.subItems"
              :key="i"
              :value="$router.history.current.path === subItem.route">
              <v-list-tile-content>
                <v-list-tile-title v-text="subItem.title"></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
        </div>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
      clipped-left
      color="blue">
      <v-toolbar-side-icon></v-toolbar-side-icon>
      <v-toolbar-title v-text="title"></v-toolbar-title>
    </v-toolbar>
    <v-content>
      <transition
        mode="out-in"
        :enter-active-class="'animated ' + drawerItems[routerIndex].enter"
        :leave-active-class="'animated ' + drawerItems[routerIndex].leave">
        <router-view
          :style="{'animation-duration': drawerItems[routerIndex].transDuration}"
          :stepIndex="stepIndex"
          @prevRoute="prevRoute"
          @nextRoute="nextRoute"/>
      </transition>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      title: 'LboT 2018 여름학기 연구발표',
      routerIndex: 0,
      stepIndex: 0,
      drawerItems: [
        {
          icon: 'home',
          title: '메인',
          route: '/',
          enter: 'fadeInUp',
          leave: 'fadeOutUp',
          transDuration: '.5s'
        },
        {
          icon: 'description',
          title: 'Quest',
          route: '/quests',
          enter: 'fadeInUp',
          leave: 'fadeOutUp',
          transDuration: '.5s',
          subItems: [{
            title: 'MR-6',
            route: '/quests/MR6'
          },
          {
            title: '알고리즘 리딩',
            route: '/quests/AlgoRead'
          },
          {
            title: '코딩',
            route: '/quests/Coding'
          },
          {
            title: '백권아카데미',
            route: '/quests/100Academy'
          }]
        },
        {
          icon: 'explore',
          title: 'SDL',
          route: '/sdls',
          enter: 'fadeInUp',
          leave: 'fadeOutUp',
          transDuration: '.5s',
          subItems: [{
            title: '택톡',
            route: '/sdls/TagTalk'
          },
          {
            title: '카페 앱',
            route: '/sdls/Cafe'
          }]
        },
        {
          icon: 'check',
          title: '총평',
          route: '/summary',
          enter: 'fadeInUp',
          leave: 'fadeOutUp',
          transDuration: '.5s'
        },
        {
          icon: 'list',
          title: '다음 학기',
          route: '/nextterm',
          enter: 'fadeInUp',
          leave: 'fadeOutUp',
          transDuration: '.5s'
        }
      ]
    }
  },
  created() {
    window.onkeydown = event => {
      switch (event.keyCode) {
        case 37:
          this.stepIndex--
          break
        case 39:
          this.stepIndex++
      }
    }
  },
  methods: {
    prevRoute() {
      if (this.routerIndex > 0) {
        this.$router.push(this.drawerItems[--this.routerIndex].route)
      }
      this.stepIndex = 0
    },
    nextRoute() {
      if (this.routerIndex < this.drawerItems.length - 1) {
        this.$router.push(this.drawerItems[++this.routerIndex].route)
        this.stepIndex = 0
      } else {
        this.stepIndex--
      }
    },
    moveToRoute(index) {
      this.$router.push(this.drawerItems[index].route)
      this.routerIndex = index
      this.stepIndex = 0
    }
  }
}
</script>

<style>
html {
  overflow: hidden;
}

.container {
  height: 100%;
}

h1 {
  font-size: 50px;
  font-weight: 450;
}

h3 {
  font-size: 25px;
  font-weight: 400;
}
</style>
