<template>
  <v-container fluid>
    <transition
      mode="out-in"
      enter-active-class="animated slideInRight"
      leave-active-class="animated slideOutLeft">
      <router-view style="animation-duration: .5s"></router-view>
    </transition>
  </v-container>
</template>

<script>
import Slide from '../mixins/Slide'

export default {
  mixins: [Slide],
  data() {
    return {
      stepDatas: [
        {
          path: '/quests'
        },
        {
          path: '/quests/MR6'
        },
        {
          path: '/quests/AlgoRead'
        },
        {
          path: '/quests/Coding'
        },
        {
          path: '/quests/100Academy'
        }
      ]
    }
  },
  watch: {
    stepIndex() {
      this.$router.push(this.stepDatas[this.stepIndex].path)
    }
  }
}
</script>

<style>
h1 {
  font-size: 50px;
  font-weight: 450
}

h4 {
  font-size: 20px;
  font-weight: 400
}
</style>
