import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

import Quests from './views/Quests.vue'
import QMain from './views/Quests/Main.vue'
import QMR6 from './views/Quests/MR6.vue'
import QAlgoRead from './views/Quests/AlgoRead.vue'
import QCoding from './views/Quests/Coding.vue'
import Q100Academy from './views/Quests/100Academy.vue'

import SDLs from './views/SDLs.vue'
import SMain from './views/SDLs/Main.vue'
import STagTalk from './views/SDLs/TagTalk.vue'
import SCafe from './views/SDLs/Cafe.vue'

import Summary from './views/Summary.vue'
import NextTerm from './views/NextTerm.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
      path: '/',
      component: Home
    },
    {
      path: '/quests',
      component: Quests,
      children: [
        {
          path: '',
          component: QMain
        },
        {
          path: 'MR6',
          component: QMR6
        },
        {
          path: 'AlgoRead',
          component: QAlgoRead
        },
        {
          path: 'Coding',
          component: QCoding
        },
        {
          path: '100Academy',
          component: Q100Academy
        }
      ]
    },
    {
      path: '/sdls',
      component: SDLs,
      children: [
        {
          path: '',
          component: SMain
        },
        {
          path: 'TagTalk',
          component: STagTalk
        },
        {
          path: 'Cafe',
          component: SCafe
        }
      ]
    },
    {
      path: '/summary',
      component: Summary
    },
    {
      path: '/nextterm',
      component: NextTerm
    }
  ]
})